<template>
  <div id="price-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="priceFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('PRICE') }}</label>
          <div class="flex">
            <div class="flex mr-2">
              <vs-input-number v-model="price" min="0"/>
            </div>
            <div class="flex-grow">
              <currency-lookup :title="$t('CURRENCY')" valueField="uomId" displayField="description" @select-record="selectCurrencyRecord"/>
            </div>
          </div>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('BILLING_IDENTIFIER') }}</label>
          <vs-input class="w-full" v-model="billingIdentifier"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('PARTY_CLASS') }}</label>
          <party-class-lookup :title="$t('PARTY_CLASS')" valueField="partyClassificationId" displayField="description" @select-record="selectPartyClassRecord"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('EXTERNAL_PARTY_ID') }}</label>
          <vs-input class="w-full" v-model="externalPartyId"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="fromDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="toDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>

      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createReportPrice" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hidePriceForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="priceTableData" :noDataText="priceTableNoDataText" :sst="true" search @search="filterPriceTable" @sort="orderPriceTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showPriceForm" :disabled="priceFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshPriceTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="priceTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ priceTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="priceTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="price">
          {{ this.$t('PRICE') }}
        </vs-th>
        <vs-th sort-key="billingIdentifier">
          {{ this.$t('BILLING_IDENTIFIER') }}
        </vs-th>
        <vs-th sort-key="partyClassificationDescription">
          {{ this.$t('PARTY_CLASS') }}
        </vs-th>
        <vs-th sort-key="externalPartyId">
          {{ this.$t('EXTERNAL_PARTY_ID') }}
        </vs-th>
        <vs-th sort-key="fromDate">
          {{ this.$t('FROM_DATE') }}
        </vs-th>
        <vs-th sort-key="toDate">
          {{ this.$t('TO_DATE') }}
        </vs-th>
        <vs-th>
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].price">
            {{ data[index].price | currency('') | placeholder('-') }} {{ data[index].currencyAbbreviation | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].billingIdentifier">
            {{ data[index].billingIdentifier | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].partyClassificationDescription">
            {{ data[index].partyClassificationDescription | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].externalPartyId">
            {{ data[index].externalPartyId | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].fromDate">
            {{ data[index].fromDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].toDate">
            {{ data[index].toDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].options">
            <vs-button class="p-3 mr-4" color="danger" @click="expireReportPrice(data[index])">{{ $t('EXPIRE') }}</vs-button>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="priceTableTotalPages" v-model="priceTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import ReportService from '@/services/report/ReportService'
import PartyClassLookup from '@/views/nucleus/party-class/PartyClassLookup'
import CurrencyLookup from '@/views/nucleus/currency/CurrencyLookup'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'report-price',
  mixins: [DatePickerMixin, LoadingMixin, ListLayoutMixin],
  components: {
    PartyClassLookup,
    CurrencyLookup,
    Datepicker
  },
  props: {
    reportId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      priceTableData: [],
      priceTableFilter: null,
      priceTableFilterTimer: null,
      priceTableOrderBy: null,
      priceTableContainer: 'price-loading-container',
      priceTableBusy: false,
      priceTableTotalRows: 0,
      priceTableCurrentPage: 1,
      priceTablePerPage: 10,
      priceFormVisible: false,
      partyClassificationId: '',
      externalPartyId: '',
      price: 0,
      currencyUomId: '',
      billingIdentifier: '',
      fromDate: null,
      toDate: null
    }
  },
  computed: {
    priceTableNoDataText: function () {
      return this.priceTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    priceTableTotalPages: function () {
      return Math.ceil(this.priceTableTotalRows / this.priceTablePerPage)
    },
    priceTablePagingInfo: function () {
      return this.pagingInfo(this.priceTableCurrentPage, this.priceTablePerPage, this.priceTableTotalRows)
    }
  },
  methods: {
    loadPriceTableData () {
      let self = this
      this.priceTableBusy = true
      ReportService.reportPrices({
        pageIndex: this.priceTableCurrentPage - 1,
        pageSize: this.priceTablePerPage,
        filter: this.priceTableFilter,
        orderByField: this.priceTableOrderBy,
        reportId: this.reportId
      }).then(function (response) {
        self.stopLoading(self.priceTableContainer)
        self.priceTableBusy = false
        self.priceTableTotalRows = response.data.totalRows
        self.priceTableData = response.data.reportPriceList || []
      })
    },
    refreshPriceTable () {
      this.loadPriceTableData()
    },
    filterPriceTable (filter) {
      let self = this
      if (this.priceTableFilter !== filter) {
        clearTimeout(this.priceTableFilterTimer)
        this.priceTableFilterTimer = setTimeout(() => {
          self.priceTableFilter = filter
          self.loadPriceTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.priceTableFilterTimer)
      }
    },
    orderPriceTable (orderBy, active) {
      this.priceTableOrderBy = (active ? '' : '-') + orderBy
      this.loadPriceTableData()
    },
    selectPartyClassRecord (record) {
      this.partyClassificationId = record.partyClassificationId || ''
    },
    selectCurrencyRecord (record) {
      this.currencyUomId = record.uomId || ''
    },
    showPriceForm () {
      this.priceFormVisible = true
    },
    hidePriceForm () {
      this.priceFormVisible = false
    },
    createReportPrice () {
      let self = this
      ReportService.createReportPrice({
        reportId: this.reportId,
        partyClassificationId: this.partyClassificationId,
        externalPartyId: this.externalPartyId,
        price: this.price,
        currencyUomId: this.currencyUomId,
        billingIdentifier: this.billingIdentifier,
        fromDate: this.getTime(this.fromDate),
        toDate: this.getTime(this.toDate)
      }).then(function (response) {
        self.refreshPriceTable()
        self.hidePriceForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    expireReportPrice (price) {
      let self = this
      ReportService.expireReportPrice({
        reportId: this.reportId,
        priceId: price.priceId
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshPriceTable()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.loadPriceTableData()
  },
  watch: {
    priceTableCurrentPage: function () {
      this.refreshPriceTable()
    },
    priceTablePerPage: function () {
      this.refreshPriceTable()
    }
  }
}
</script>
