<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isReportAll')">{{ $t('MODIFY') }}</vs-button>
      </div>
      <div class="row hover mb-base">
        <p class="info-row">
          <span>{{ $t('ID') }}:</span>
          <span>{{ report.reportId }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('REPORT_NAME') }}:</span>
          <span>
            <nv-localization-popup :active="namePopupActive" v-on:close="namePopupActive = false" path="/s1/report/report/localization" popupRef="localizationModal" :pkValue="this.$route.params.id" fieldName="reportName" id="reportName"/>
            <vs-button class="m-0 p-0" color="primary" type="flat" @click="namePopupActive = true"><i class="far fa-globe-africa mr-1 text-primary"></i></vs-button>
            {{ report.reportName }}
          </span>
        </p>
        <p class="info-row">
          <span>{{ $t('TYPE') }}:</span>
          <span>{{ report.typeDescription | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('CATEGORY') }}:</span>
          <span>{{ report.categoryName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('PSEUDO_ID') }}:</span>
          <span>{{ report.pseudoId | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="report.iconClass">
          <span>{{ $t('ICON_CLASS') }}:</span>
          <span>{{ report.iconClass | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="report.serviceName">
          <span>{{ $t('SERVICE_NAME') }}:</span>
          <span>{{ report.serviceName | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="report.dataDocumentId">
          <span>{{ $t('DATA_DOCUMENT_ID') }}:</span>
          <span>{{ report.dataDocumentId | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="report.dataField">
          <span>{{ $t('DATA_FIELD') }}:</span>
          <span>{{ report.dataField | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('CREATION_DATE') }}:</span>
          <span>{{ report.creationDate | formatUnixTimestamp }}</span>
        </p>
      </div>

      <vs-collapse type="border" class="mb-base" v-if="report.browseNotes || report.runNotes">
        <vs-collapse-item v-if="report.browseNotes">
          <div slot="header">
            {{ $t('BROWSE_NOTES') }}
          </div>
          <div v-html="report.browseNotes"></div>
        </vs-collapse-item>
        <vs-collapse-item v-if="report.runNotes">
          <div slot="header">
            {{ $t('RUN_NOTES') }}
          </div>
          <div v-html="report.runNotes"></div>
        </vs-collapse-item>
      </vs-collapse>

      <vs-tabs>
        <vs-tab :label="$t('APPS')">
          <report-app @show-alert="showAlert" :reportId="reportId"/>
        </vs-tab>
        <vs-tab :label="$t('PRICES')">
          <report-price @show-alert="showAlert" :reportId="reportId"/>
        </vs-tab>
        <vs-tab :label="$t('INPUT_PARAMETERS')">
          <report-input-parameter @show-alert="showAlert" :reportId="reportId" :reportType="report.reportTypeEnumId"/>
        </vs-tab>
        <vs-tab :label="$t('OUTPUT_PARAMETERS')" v-if="report.reportTypeEnumId === 'RPT_TYPE_SERVICE_INLINE' || report.reportTypeEnumId === 'RPT_TYPE_DATA_DOCUMENT'">
          <report-output-parameter @show-alert="showAlert" :reportId="reportId"/>
        </vs-tab>
      </vs-tabs>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import CodeEditorMixin from '@/mixins/commons/CodeEditorMixin'
import ReportService from '@/services/report/ReportService'
import NvLocalizationPopup from '@/components/nv-localization/NvLocalizationPopup'
import ReportInputParameter from '@/views/report/report/report-view/ReportInputParameter'
import ReportOutputParameter from '@/views/report/report/report-view/ReportOutputParameter'
import ReportPrice from '@/views/report/report/report-view/ReportPrice'
import ReportApp from '@/views/report/report/report-view/ReportApp'

export default {
  name: 'report-view',
  mixins: [AlertMixin, CodeEditorMixin],
  components: {
    NvLocalizationPopup,
    ReportInputParameter,
    ReportOutputParameter,
    ReportPrice,
    ReportApp
  },
  data: function () {
    return {
      report: {},
      reportId: this.$route.params.id,
      namePopupActive: false,
      descriptionPopupActive: false
    }
  },
  methods: {
    async load () {
      let self = this
      ReportService.reports({
        reportId: this.reportId
      }).then(function (response) {
        self.report = response.data.reportList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyReport', params: { id: this.reportId } })
    }
  },
  mounted () {
    this.load()
  }
}
</script>
