<template>
  <div id="output-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="parameterFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('PARAMETER_NAME') }}</label>
          <vs-input class="w-full" v-model="parameter.parameterName"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('TYPE') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="parameter.parameterTypeEnumId">
            <vs-select-item v-for="(enumeration, index) in parameterTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('DISPLAY_NAME') }}</label>
          <vs-input class="w-full" v-model="parameter.displayName"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('POSITION') }}</label>
          <div class="flex">
            <vs-input-number v-model="parameter.position" min="0"/>
          </div>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <vs-checkbox v-model="parameter.disabled">{{ $t('DISABLED') }}</vs-checkbox>
        </div>
      </div>

      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="putReportParameter(parameter)" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideParameterForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="parameterTableData" :noDataText="parameterTableNoDataText" :sst="true" search @search="filterParameterTable" @sort="orderParameterTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showParameterForm" :disabled="parameterFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshParameterTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="parameterTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ parameterTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="parameterTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="position">#</vs-th>
        <vs-th>
          {{ this.$t('PARAMETER_NAME') }}
        </vs-th>
        <vs-th>
          {{ this.$t('DISPLAY_NAME') }}
        </vs-th>
        <vs-th>
          {{ this.$t('TYPE') }}
        </vs-th>
        <vs-th>
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].position">
            {{ data[index].position }}
          </vs-td>
          <vs-td :data="data[index].parameterName">
            <span :class="disabledClass(data[index])">{{ data[index].parameterName }}</span>
          </vs-td>
          <vs-td :data="data[index].displayName">
            {{ data[index].displayName | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].parameterTypeDescription">
            {{ data[index].parameterTypeDescription | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].options">
            <div class="flex">
              <vs-button class="p-3 mr-4" color="primary" @click="showParameterForm(data[index])" icon-pack="far" icon="fa-pen">{{ $t('MODIFY') }}</vs-button>
            </div>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="parameterTableTotalPages" v-model="parameterTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import ReportService from '@/services/report/ReportService'
import EnumerationService from '@/services/commons/EnumerationService'

export default {
  name: 'report-output-parameter',
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    reportId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      parameterTableData: [],
      parameterTableFilter: null,
      parameterTableFilterTimer: null,
      parameterTableOrderBy: 'position',
      parameterTableContainer: 'output-loading-container',
      parameterTableBusy: false,
      parameterTableTotalRows: 0,
      parameterTableCurrentPage: 1,
      parameterTablePerPage: 10,
      parameterFormVisible: false,
      parameter: {}
    }
  },
  computed: {
    parameterTableNoDataText: function () {
      return this.parameterTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    parameterTableTotalPages: function () {
      return Math.ceil(this.parameterTableTotalRows / this.parameterTablePerPage)
    },
    parameterTablePagingInfo: function () {
      return this.pagingInfo(this.parameterTableCurrentPage, this.parameterTablePerPage, this.parameterTableTotalRows)
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'ReportParameterType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.parameterTypes = response.data.enumerationList || []
      })
    },
    loadParameterTableData (loadingContainerId) {
      let self = this
      this.parameterTableBusy = true
      this.parameterTableContainer = loadingContainerId || this.parameterTableContainer
      this.startLoading(this.parameterTableContainer)
      ReportService.reportParameters({
        pageIndex: this.parameterTableCurrentPage - 1,
        pageSize: this.parameterTablePerPage,
        filter: this.parameterTableFilter,
        orderByField: this.parameterTableOrderBy,
        reportId: this.reportId,
        parameterDirectionEnumId: 'RPT_PARAM_DIR_OUT'
      }).then(function (response) {
        self.stopLoading(self.parameterTableContainer)
        self.parameterTableBusy = false
        self.parameterTableTotalRows = response.data.totalRows
        self.parameterTableData = response.data.reportParameterList || []
      })
    },
    refreshParameterTable () {
      this.loadParameterTableData()
    },
    filterParameterTable (filter) {
      let self = this
      if (this.parameterTableFilter !== filter) {
        clearTimeout(this.parameterTableFilterTimer)
        this.parameterTableFilterTimer = setTimeout(() => {
          self.parameterTableFilter = filter
          self.loadParameterTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.parameterTableFilterTimer)
      }
    },
    orderParameterTable (orderBy, active) {
      this.parameterTableOrderBy = (active ? '' : '-') + orderBy
      this.loadParameterTableData()
    },
    showParameterForm (parameter) {
      if (parameter.parameterId) {
        this.parameter = parameter
        this.parameter.mandatory = (parameter.mandatory === 'Y')
        this.parameter.disabled = (parameter.disabled === 'Y')
      } else {
        this.parameter = {
          parameterName: '',
          displayName: '',
          parameterTypeEnumId: 'RPT_PARAM_TYPE_TEXT',
          parameterTypes: [],
          position: 0,
          mandatory: false,
          disabled: false
        }
      }
      this.parameterFormVisible = true
    },
    hideParameterForm () {
      this.parameterFormVisible = false
      this.refreshParameterTable()
    },
    putReportParameter (parameter) {
      if (parameter.parameterId) {
        this.modifyReportParameter()
      } else {
        this.createReportParameter()
      }
      this.hideParameterForm()
    },
    createReportParameter () {
      let self = this
      ReportService.createReportParameter({
        reportId: this.reportId,
        parameterName: this.parameter.parameterName,
        parameterDirectionEnumId: 'RPT_PARAM_DIR_OUT',
        parameterTypeEnumId: this.parameter.parameterTypeEnumId,
        displayName: this.parameter.displayName,
        position: this.parameter.position,
        disabled: this.parameter.disabled ? 'Y' : 'N'
      }).then(function (response) {
        self.refreshParameterTable()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    modifyReportParameter () {
      let self = this
      ReportService.modifyReportParameter({
        reportId: this.reportId,
        parameterId: this.parameter.parameterId,
        parameterName: this.parameter.parameterName,
        parameterDirectionEnumId: 'RPT_PARAM_DIR_OUT',
        parameterTypeEnumId: this.parameter.parameterTypeEnumId,
        displayName: this.parameter.displayName,
        position: this.parameter.position,
        mandatory: this.parameter.mandatory ? 'Y' : 'N',
        disabled: this.parameter.disabled ? 'Y' : 'N'
      }).then(function (response) {
        self.refreshParameterTable()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.load()
    this.loadParameterTableData()
  },
  watch: {
    parameterTableCurrentPage: function () {
      this.refreshParameterTable()
    },
    parameterTablePerPage: function () {
      this.refreshParameterTable()
    }
  }
}
</script>
